@import "../../assets/scss/style.scss";

.odyssey_main_container {
  .odyssey_container {
    color: white;
    display: grid;
    // grid-template-areas: "left middle  right";
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
    padding: 10px;
    .odyssey_left {
      grid-area: "left";
      .left_main_card_container {
        .left_main_card {
          .profile-card {
            border-radius: 26px;
            box-shadow: 0px -1px 24px 0px rgba(0, 0, 0, 0.25);
            background-color: #1a2730;
            display: flex;
            max-width: 505px;
            padding-bottom: 35px;
            flex-direction: column;
            align-items: center;
            font-size: 24px;
            color: #fff;
            text-align: center;
          }

          .profile-background {
            aspect-ratio: 3.13;
            object-fit: cover;
            width: 100%;
          }

          @media (max-width: 991px) {
            .profile-background {
              max-width: 100%;
            }
          }

          .profile-avatar {
            aspect-ratio: 0.95;
            object-fit: cover;
            width: 100px;
            border-radius: 50%;
            margin-top: -50px;
            max-width: 100%;
          }

          .profile-description {
            font-weight: 400;
            margin-top: 55px;
            color: #fff;
            text-align: center;
            font-size: 24px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
          }

          @media (max-width: 991px) {
            .profile-description {
              margin-top: 40px;
            }
          }

          .profile-divider {
            border-color: rgba(255, 255, 255, 1);
            border-style: solid;
            border-width: 0px;
            background-color: #fff;
            width: 100%;
            min-height: 1px;
            margin-top: 38px;
          }

          @media (max-width: 991px) {
            .profile-divider {
              max-width: 100%;
            }
          }

          .profile-stats {
            display: flex;
            margin-top: 35px;
            // width: 325px;
            max-width: 100%;
            justify-content: space-between;
            gap: 20px;
          }

          @media (max-width: 991px) {
            .profile-stats {
              margin-top: 40px;
              flex-wrap: wrap;
            }
          }

          .profile-stat {
            display: flex;
            flex-direction: column;
            flex: 1;
            padding: 0 20px;
          }

          .profile-stat-value {
            color: #fff;
            text-align: center;
            font-size: 20px;
            font-style: normal;
            font-weight: 800;
            line-height: normal;
          }

          .profile-stat-label {
            color: #fff;
            text-align: center;
            font-size: 20px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
          }

          //   User card
          .user_follow_card_main_container {
            .user_follow_container {
              .profile-card {
                border-radius: 26px;
                background-color: #1a2730;
                // max-width: 505px;
                // padding: 80px 19px 0;
                margin-top: 10px;
                padding-bottom: 15px;
              }

              .profile-card__content {
                display: flex;
                gap: 20px;
              }

              @media (max-width: 991px) {
                .profile-card__content {
                  flex-direction: column;
                  align-items: stretch;
                  gap: 0;
                }
              }

              .profile-card__profiles {
                display: flex;
                flex-direction: column;
                line-height: normal;
                margin-left: 0;
                padding: 7px 15px;
                
                .left {
                  display: flex;
                  align-items: center;
                  justify-content: space-between;
                  gap: 5px;
                }
              }

              @media (max-width: 991px) {
                .profile-card__profiles {
                  width: 100%;
                }
              }

              .profile-card__profile {
                display: flex;
                flex-grow: 1;
                gap: 17px;
                align-items: center;
                justify-content: space-between;
              }

              @media (max-width: 991px) {
                .profile-card__profile {
                  margin-top: 40px;
                }
              }

              .profile-card__avatar-group {
                display: flex;
                flex-direction: column;
                align-items: center;
                flex-basis: 0%;
              }

              .profile-card__avatar {
                aspect-ratio: 1;
                object-fit: cover;
                width: 55px;
                border-radius: 50%;
                margin-top: 18px;
              }

              .profile-card__info {
                display: flex;
                flex-direction: column;
                font-size: 25px;
                color: #fff;
                font-weight: 800;
                text-align: center;
                flex-grow: 1;
                flex-basis: 0%;
                margin: auto 0;
              }

              @media (max-width: 991px) {
                .profile-card__info {
                  white-space: initial;
                }
              }

              .profile-card__name {
                color: #fff;
                font-size: 20px;
                font-style: normal;
                font-weight: 800;
                line-height: normal;
                text-align: left;
              }

              .profile-card__username {
                color: rgba(255, 255, 255, 0.66);
                font-size: 20px;
                font-style: normal;
                font-weight: 400;
                line-height: normal;
                text-align: left;
              }

              .profile-card__actions {
                display: flex;
                flex-direction: column;
                line-height: normal;
                width: 25%;
                margin-left: 20px;
              }

              @media (max-width: 991px) {
                .profile-card__actions {
                  width: 100%;
                }
              }

              .profile-card__follow-btn {
                font-family: Mplus 1p, sans-serif;
                border-radius: 10px;
                background-color: #d9d9d9;
                color: #000;
                font-size: 13px;
                font-weight: 800;
                text-align: center;
                padding: 8px 17px;
                // margin-top: 57px;
                cursor: pointer;
                border: none;
              }

              .profile-card__follow-btn:first-child {
                // margin-top: 72px;
              }

              @media (max-width: 991px) {
                .profile-card__follow-btn {
                  //   margin-top: 40px;
                  white-space: initial;
                }

                .profile-card__follow-btn:first-child {
                  //   margin-top: 40px;
                }
              }
            }
          }
        }
      }
    }
    .news_middle {
    grid-column: 2/4;

      .button_container{
        padding-bottom: 15px;

        .unorder-container{
            display: flex;
            justify-content: center;
            align-items: center;
            column-gap: 20px;
            list-style-type: none;
            margin: 0;
            padding: 0;

            .switch-trueButton{
                background-color: rgb(255, 0, 0);
                color: #ff0b0b;
                font-weight: 700;
                padding: 11px 35px;
                border-radius: 10px;
                font-size: 0.875rem; /* 14px */
                line-height: 1.25rem; /* 20px */    

            }
            .switch-falseButton{
                background-color: #27343e;
                color: #fff;
                font-weight: 700;
                padding: 8px 35px;
                border-radius: 10px;
                font-size: 0.875rem; /* 14px */
                line-height: 1.25rem; /* 20px */ 

            }
        }
      }

      //event_post
      .event_content{
        
        .event_card{
          border-radius: 26px;
          box-shadow: 0px 11px 35px 0px rgba(0, 0, 0, 0.25);
          background-color: #1a2730;
          display: flex;
          flex-direction: column;
          max-width: 705px;
          padding: 27px;
          margin-top: 10px;

          @media (max-width:991px){
            .post-card{
              padding: 0 20px;

            }
          }

          .event_header{
            display: flex;
            justify-content: space-between;
            gap:20px;
            width: 100%;
            align-items: center;


            .user_info{
              display: flex;
              gap:11px;
              justify-content: center;
              align-items: center;
              .user_avatar{
                aspect-ratio: 1;
                border-radius: 50%;
                object-fit: cover;
                object-position: center;
                width: 55px;
              }
              .username{
                color:#fff;
                text-align: center;
                font-size:18px ;
                font-style: normal;
                font-weight: 600;
                margin: auto 0;

              }
            }
            .follow-button{
              background-color: $secondary-color;
              border-radius: 10px;
              color: #000;
              padding: 6px 20px;
              font-weight: 500;
              font-style: normal;
              line-height: normal;
              height: fit-content;
              text-align: center;
              font-size: 15px;

            }
          }
          .event_content{
            margin-top: 20px;

            @media (max-width:991px){
              max-width: 100%;
            }
            .event_text_content{

              .event_text{
                color: #fff;
                font-size:16px ;
                font-style: normal;
                font-weight: 400;
                line-height: 25px ;
                .event_text_end{
                  font-weight: 700;
                  font-size: 18px;
                  color: #33CEF0;
                  opacity: 46%;

                }

              }
            }
          }
          .event_footer{
            margin: 20px 0 10px;
            width: 100%;
            
            .footer_items{
              gap :22px;
            display: flex;
            align-items: center;
            flex-direction: row;
            .like_count{
              display: flex;
              flex-direction: row;
              background-color: rgba(92, 102, 108, 0.58);
              border-radius: 21px;
              padding: 5px 22px;
              font-size: 15px;
              font-weight: 500;
              gap: 11px;
              white-space: nowrap;
              .like_icon{
                aspect-ratio: 1.06;
                object-fit: cover;
                object-position: center;
                width: 22px;
              }
              .like-number {
                flex-grow: 1;
                font-family: Mplus 1p, sans-serif;
                margin: auto 0;
              }
              .dislike-icon{
                aspect-ratio: 1.09;
                object-fit: cover;
                width: 22px;
                margin-left: 15px;

              }
              
            }
            .user-avatar-small {
              aspect-ratio: 1;
              object-fit: cover;
              object-position: center;
              width: 22px;
            }
            .footer_dot{
              width: 5px;
              height: 5px;
              border-radius: 99px;
              background-color: #d9d9d9;
            }
            .footer_reply{
              font-size: 18px;
              font-weight: 600;
              font-style: normal;
              line-height: 12px;
              color: #d9d9d9;
            }

            }
          }
        }
      }
      
