      // News_post
      .news_mainBody{
        background-color: #1a2730;
        border-radius: 26px;
        padding: 0px 15px 0 30px;
        color:#fff;
        margin-top:10px;
        
        .news_card{
            display: flex;
            flex-direction: row;
            padding: 35px 0;
            gap: 30px;


        }
        .news_cardleft{
            width:50% ;
            .img_newscardleft{
                max-width: 220px;
                height: 220px;
                border-radius: 22px;
                object-fit: cover;
            }
            .heading_newscardleft{
                display: flex;
                flex-direction: row;
                align-items: center;
                margin: 15px 0;
                gap: 10px;
                .profile_newscardleft{
                    width:20px;
                    height:20px;
                    background-color: chocolate;


                }
            }
            .time_newscardleft{
                display: flex;
                flex-direction: row;
                gap: 7px;
                align-items: center;
                opacity: 50%;
                font-weight: 300;
                .gap_aftertime{
                    background-color: #ffffff;
                    opacity: 50%;
                    width: 5px;
                    height: 5px;
                    border-radius: 999px;
                }
            }
        }
        .news_cardright{
            width: 50%;
            .container_newscardright{
                padding-bottom: 30px;
                .heading_newscardright{
                display: flex;
                flex-direction: row;
                align-items: center;
                gap: 10px;
                margin-bottom: 6px;
                .profile_newscardright{
                    background-color: darkmagenta;
                    width: 13px;
                    height: 13px;


                }
            }
            .para_newscardright{
                font-weight: 500;
                margin-top: 7px;
                padding: 0px;
            }
            .time_newscardright{
                display: flex;
                flex-direction: row;
                gap: 7px;
                align-items: center;
                justify-items: center;
                opacity: 50%;
                margin-top: 0px;
                font-size: smaller;

                .gap_aftertime{
                    background-color: #ffffff;
                    opacity: 50%;
                    width: 5px;
                    height: 5px;
                    border-radius: 999px;
                }
            }
            
            }
        }

      }

