              font-size: 25px;
              font-weight: 700;
              max-width: 374px;
              padding: 40px 45px;
              text-align: center;
              white-space: nowrap;
            }
            .explore-heading {
              color: #fff;
              text-align: left;
              font-size: 27px;
              font-style: normal;
              font-weight: 800;
              line-height: normal;
            }
            .menu-item {
              align-items: center;
              background-color: #27343e;
              border-radius: 10px;
              font-family: Mplus 1p Bold, -apple-system, Roboto, Helvetica,
                sans-serif;
              justify-content: center;
              margin-top: 20px;
              padding: 9px 40px;

              text-align: center;
              font-size: 22px;
              font-style: normal;
              font-weight: 700;
              line-height: normal;
              cursor: pointer;
            }
            .menu-item.active {
              background-color: #fff;
              color: #1a2730;
            }
          }

          .sponsored-post {
            margin-top: 10px;
            border-radius: 26px;
            box-shadow: 0px 11px 35px 0px rgba(0, 0, 0, 0.25);
            background-color: #1a2730;
            display: flex;
            flex-direction: column;
            max-width: 374px;
            padding: 24px 25px 54px;
          }

          .sponsored-label {
            color: rgba(255, 255, 255, 0.43);
            font: 500 19px Mplus 1p, sans-serif;
          }

          .author-info {
            display: flex;
            align-items: center;
            gap: 12px;
            margin-top: 17px;
          }

          .author-image {
            width: 35px;
            height: 35px;
            border-radius: 50%;
            object-fit: cover;
          }

          .author-username {
            color: #fff;
            font: 700 16px Mplus 1p Bold, -apple-system, Roboto, Helvetica,
              sans-serif;
            margin-right: auto;
          }

          .verified-badge {
            width: 25px;
            height: 25px;
            margin-top: 7px;
          }

          .follow-button {
            background-color: #d9d9d9;
            border: none;
            border-radius: 10px;
            color: #000;
            cursor: pointer;
            font: 500 14px Mplus 1p, sans-serif;
            padding: 11px 17px;
          }

          .post-content {
            display: flex;
            gap: 15px;
            margin-top: 29px;
          }

          .post-image {
            width: 100px;
            max-width: 100%;
            aspect-ratio: 0.99;
            object-fit: cover;
          }

          .post-description {
            color: #fff;
            font-size: 15px;
            font-style: normal;
            font-weight: 400;
            line-height: normal;
          }
        }
      }
    }
  }

    
  
